<template>
    <div class="option-section" :class="{ 'section-collapsed': isCollapsed }">
        <div class="section-header" @click="toggleCollapse()">
            <span class="section-collapse-icon" v-html="isCollapsed ? upArrow : downArrow"></span>
            <h3>{{ title }}</h3>
        </div>
        <div class="section-content">
            <div v-for="(field, propName) in fields" :key="propName">
                <div v-if="field.type === 'checkbox'">
                    <SliderCheckbox
                        :checkboxValue="field.value" 
                        @valueChanged="checkboxUpdate(propName, $event)"
                    />
                </div>
                
                <div v-if="field.type === 'numeric'">
                    <p>{{ field.name }}</p>
                    <input type="number" min="1" v-model="field.value" @change="emitUpdate"/>
                </div>
            </div>
        </div>
        <hr>
    </div>  
</template>

<script>
import SliderCheckbox from './SliderCheckbox.vue'
export default {
    components: {
        SliderCheckbox
    },
    props: {
        title: {
            type: String,
            default() {
                return ''
            }
        },
        fields: {
            type: Object,
            default() {
                return {}
            }
        },
        collapsed: {
            type: Boolean,
            default(){
                return true
            }
        }
    },
    data() {
        return {
            filters: this.fields,
            isCollapsed: false,
            downArrow: '&#x25BC;',
            upArrow: '&#x25B2;'
        };
    },
    methods: {
        toggleCollapse() {
            this.isCollapsed = !this.isCollapsed;
        },
        emitUpdate(){ 
            this.$emit('filterEvent', this.filters);
        },
        checkboxUpdate(propName, newValue) {
            this.filters[propName].value = newValue;
            this.emitUpdate();
        }
    },
    mounted() {
        this.isCollapsed = this.collapsed;
    }
}
</script>
<style>
/* START OF Collapsable option sections */
.option-section {
    margin-bottom: 20px;
}

.section-header {
    cursor: pointer;
}

.section-collapse-icon {
    float: right;
}

.section-content {
    display: block;
    padding: 10px;
    /*transition: max-height 0.2s ease-out;*/
    overflow: hidden;
    max-height: 100%; /* Initial height of the collapsible content */
}
.section-collapsed .section-content {
    max-height: 0;
    padding-top: 0;
    padding-bottom: 0;
}

input {
    width: 98%;
    padding: 5px;
    font-family: Avenir, Helvetica, Arial, sans-serif;
    font-size: large;
    border-radius: 8px;
    border-style: none;
    margin-bottom: 10px;
}

p {
    margin: 0px;
}

h3 { 
    margin: 0px;
}
/* END OF Collapsable option sections */
</style>