<template>
    <div class="container">
        <button class="menu-toggle">â˜°</button>
        <div class="sidebar">
            <img id="gt-logo" alt="GTRaceNight" src="../assets/GTRaceNightLogo.webp">
            <h2>Race Options</h2>

            <SectionOptions 
                :title="'Performance Points'"                
                :fields="performancePointsFilters"
                :collapsed="false"
                @filterEvent="updateFilters('performancePointsFilters', $event)"
            />

            <SectionOptions 
                :title="'Year Range'"                
                :fields="yearFilters"
                @filterEvent="updateFilters('yearFilters', $event)"
            />

            <SectionOptions 
                :title="'Manufacturers'"                
                :fields="manufacturersFilters"
                @filterEvent="updateFilters('manufacturersFilters', $event)"
            />

            <button class="button-race" @click="generateRace">Race!</button>

            <div v-if="validationErrors.length">
                <p v-for="(error, index) in validationErrors" :key="index">
                    {{ error }}
                </p>
            </div>
        </div>
        <div class="main-content">
            <div class="content-header">
                <h1>GT RACE NIGHT!</h1>
            </div>
            
            <div v-if="raceGenerated">
                <img id="track-image" :src="getTrackImage(selectedTrack)" />
                <h2>{{ selectedTrack.name }}</h2>
                <h3>Performance Points: {{ performancePointsDisplay }}</h3>
                <h3>Year: {{ yearRangeDisplay }}</h3>
                <h3>Manufacturers: {{ manufacturersDisplay }}</h3>
            </div>

            <SessionHistory
                :history="history"
            />            
        </div>
    </div>
</template>
  
<script>
//const _ = require('lodash');
import trackData from '../data/tracks.json'
import manufacturersData from '../data/manufacturers.json'
import SessionHistory from './SessionHistory.vue'
import SectionOptions from './SectionOptions.vue'
export default {
    name: 'RaceNight',
    components: {
        SessionHistory,
        SectionOptions
    },
    data() {
        return {
            raceCounter: 0,
            raceGenerated: false,
            validationErrors: [],
            selectedTrack: {},
            selectedPerformancePoint: 0,
            selectedYear: 0,
            selectedYearRadius: 0,
            selectedManufacturers: [],
            manufacturers: [],
            tracks: [],
            history: [],
            performancePointsFilters: {
                active: {
                    name: 'Active',
                    type: 'checkbox',
                    value: true
                }, 
                min: {
                    name: 'Min',
                    type: 'numeric',
                    value: 550
                },
                max: {
                    name: 'Max',
                    type: 'numeric',
                    value: 900
                }
            },
            yearFilters: {
                active: {
                    name: 'Active',
                    type: 'checkbox',
                    value: true
                }, 
                min: {
                    name: 'Min',
                    type: 'numeric',
                    value: 1965
                },
                max: {
                    name: 'Max',
                    type: 'numeric',
                    value: 2024
                },
                radius: {
                    name: 'Radius',
                    type: 'numeric',
                    value: 2
                }
            },
            manufacturersFilters: {
                active: {
                    name: 'Active',
                    type: 'checkbox',
                    value: false
                }, 
                max: {
                    name: 'Max',
                    description: 'Amount of manufacturers to include',
                    type: 'numeric',
                    value: 1
                }
            }
        }
    },
    mounted() {
        this.tracks = trackData;
        this.manufacturers = manufacturersData;
    },
    methods: {
        updateFilters(key, updatedFilters){
            this[key] = updatedFilters;
        },
        getRandomInt(min = 0, max = 100, fun) {
            let ran = Math.floor(Math.random() * (max - min + 1)) + min
            console.log('Debug (' + fun + ') Min: ' + min + ' Max: ' + max + ' ran: ' + ran);
            return ran;
        },
        resetSelected(){
            this.selectedTrack = {};
            this.selectedPerformancePoint = 0;
            this.selectedYear = 0;
            this.selectedYearRadius = 0;
            this.selectedManufacturers = [];
        },
        generateRace() {
            this.raceGenerated = false;
            if(!this.formValidation()) {
                return;
            }
            
            /* Get Race Options */
            this.resetSelected();
            this.selectedTrack = this.getTrack();
            if(this.performancePointsFilters.active.value){
                this.selectedPerformancePoint = this.getPerformancePoint();
            }
            if(this.yearFilters.active.value){
                this.selectedYearRadius = this.yearFilters.radius.value;
                this.selectedYear = this.getYear();
            }
            if(this.manufacturersFilters.active.value) {
                this.selectedManufacturers = this.getManufacturers();
            }

            this.raceCounter++;
            this.saveHistory();
            this.raceGenerated = true;
        },
        saveHistory() {
            this.history.unshift({
                raceCount: this.raceCounter,
                track: this.selectedTrack,
                pp: this.selectedPerformancePoint ? this.selectedPerformancePoint : 'Any',
                year: this.selectedYear ? this.selectedYear : 'Any',
                radius: this.yearFilters.radius.value,
                manufacturers: this.manufacturersDisplay
            });
        },
        formValidation() {
            this.validationErrors = [];
            if(!Number.isInteger(this.performancePointsFilters.min.value)) {
                this.validationErrors.push('PP Min is not a number!')
            }
            if(!Number.isInteger(this.performancePointsFilters.max.value)) {
                this.validationErrors.push('PP Max is not a number!')
            }
            if(!Number.isInteger(this.yearFilters.min.value)) {
                this.validationErrors.push('Year Min is not a number!')
            }
            if(!Number.isInteger(this.yearFilters.max.value)) {
                this.validationErrors.push('Year Max is not a number!')
            }
            if(!Number.isInteger(this.yearFilters.radius.value)) {
                this.validationErrors.push('Year Radius is not a number!')
            }
            if(this.performancePointsFilters.min.value > this.performancePointsFilters.max.value) {
                this.validationErrors.push('PP Min cannot be greater than PP Max')
            }
            if(this.yearMin > this.yearMax) {
                this.validationErrors.push('PP Min cannot be greater than PP Max')
            }
            if(this.manufacturersFilters.max.value > this.manufacturers.length) {
                this.validationErrors.push('Max number of manufacturers: ' + this.manufacturers.length);
            }
            return !this.validationErrors.length ? true : false;
        },
        getTrack() {
            return this.tracks[this.getRandomInt(0, this.tracks.length - 1, 'getTrack')];
        },
        getTrackImage(track) {
            return require(`../assets/tracks/${track.image}`);
        },
        getPerformancePoint() {
            return this.getRandomInt(this.performancePointsFilters.min.value, this.performancePointsFilters.max.value, 'getPerformancePoint');
        },
        getYear() {
            return this.getRandomInt(this.yearFilters.min.value, this.yearFilters.max.value, 'getYear');
        },
        getManufacturers() {
            let mans = [];
            for (let i = 1; i <= this.manufacturersFilters.max.value; i++) {
                let randomManufacturer = this.manufacturers[this.getRandomInt(0, this.manufacturers.length - 1, 'getManufacturers')];
                if(mans.includes(randomManufacturer)) {
                    i--;
                } else {
                    mans.push(randomManufacturer);
                }
            }
            return mans.sort();
        }
    },
    computed: {
        yearRangeDisplay() {
            if(!this.selectedYear){
                return 'Any';
            }
            let lowerYear = this.selectedYear - this.selectedYearRadius;
            let upperYear = this.selectedYear + this.selectedYearRadius;
            return `${this.selectedYear} (${lowerYear} - ${upperYear})`
        },
        manufacturersDisplay() {
            return this.selectedManufacturers.length ? this.selectedManufacturers.toString().replace(/,/g, ", ") : 'Any'
        },
        performancePointsDisplay() {
            return this.selectedPerformancePoint ? this.selectedPerformancePoint : 'Any'
        }
    }

}
</script>
  
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
    display: flex;
    height: 100vh;
}

.sidebar {
    flex: 0 0 250px; /* Fixed width for the sidebar */
    background-color: #950000;
    padding: 20px;
    height: 100vh; /* Full height */
    box-sizing: border-box;
    text-align: left;
    box-shadow: 1px 1px 15px 1px #625e5e;
    z-index: 1;
    color: white;
    overflow-y: auto;
}

.main-content {
    flex-grow: 1; /* Takes up the remaining width */
    padding: 20px;
    background-color: #fff;
    box-sizing: border-box;
    text-align: center;
    color: white;
    background-image: url(https://www.gran-turismo.com/images/c/i1thu8zNgZ9sjO.jpg);
    background-position: center;
}

.menu-toggle {
    display: none; /* Hide by default */
    padding: 10px;
    font-size: 20px;
    cursor: pointer;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .container {
        flex-direction: column;
    }
    .sidebar {
        height: auto;
        flex: 0 0 50px; 
        text-align: center;
        overflow-y: unset;
    }
    .main-content {
        height: auto;
    }

    #gt-logo { 
        height: 50px !important;
    }

    #track-image {
        height: 100px !important;
    }
}
#gt-logo {
    height: 200px;
    border-radius: 20%;
}

#track-image { 
    height: 200px;
}
</style>
  